import navigation from '../../components/navigation/navigation';
export default {
	data() {
		return {
			config: {
				type: 1, //0无title，1有title
				title: "手机验证",
				back: true,
			},
			CodeStatus: false,
			CodeBtn: '获取验证码',
			phone: '',
			code: '',
		}
	},
	components: {
		navigation
	},
	methods: {
		customConduct() {
			uni.navigateBack({});
		},
		handlegetcode() {
			const mobile = /^[1][3,4,5,7,8,6][0-9]{9}$/;
			if (/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phone)) {
				this.$http
					.post('/api/base/getCheckCode/', {
						number: this.phone
					})
					.then(res => {
						this.timeOut();
						uni.showToast({
							title: '获取验证码成功',
							icon: 'none'
						});
					})
					.catch(err => {
						console.log(err);
					});
			} else {
				uni.showToast({
					title: '手机号格式错误',
					icon: 'none'
				});
			}
		},
		timeOut() {
			// 获取验证码按钮倒计时
			let t = 60000;
			this.CodeStatus = true;
			let _this = this;
			let timeOut = setInterval(function() {
				t -= 1000;
				_this.CodeBtn = t / 1000;
				if (t <= 0) {
					clearTimeout(timeOut);
					_this.CodeBtn = '获取验证码';
					_this.CodeStatus = false;
				}
			}, 1000);
		},
		handlesure(){
			if (this.code==''||this.phone==''){
				uni.showToast({
					title: '请填写完整信息',
					icon: 'none'
				});
				return;
			}
			this.$http
				.post('/api/base/bindMobile/', {
					checkcode: this.code,
					mobileNumber: this.phone
				})
				.then(res => {
					setTimeout(() => {
						uni.showToast({
							title: '绑定成功',
							icon: 'success'
						});
					}, 100);
					uni.switchTab({
						url:'../my/my'
					})
				})
				.catch(err => {
					console.log(err);
				});
		}
	},
}
